"use strict";

/**
 * @fileOverview 
 * @author dxq613
 */
var Util = require('./util');

var Theme = require('./theme/index');

var Global = {
  version: '3.5.11',
  renderer: 'canvas',
  // trackable: false,
  trackingInfo: {},
  animate: true,
  widthRatio: {
    // 
    column: 1 / 2,
    //  1/2
    rose: 0.9999999,
    //  1
    multiplePie: 1 / 1.3 // 

  },
  // path 
  showSinglePoint: false,
  connectNulls: false,
  scales: {},
  registerTheme: function registerTheme(name, theme) {
    Theme[name] = theme;
  },
  setTheme: function setTheme(theme) {
    var newTheme = {};

    if (Util.isObject(theme)) {
      newTheme = theme;
    } else if (Util.indexOf(Object.keys(Theme), theme) !== -1) {
      newTheme = Theme[theme];
    } else {
      newTheme = Theme.default;
    }

    Util.deepMix(Global, newTheme);
  }
};
Global.setTheme('default');
module.exports = Global;


